import Landing from '@/layouts/Landing'
import Home from '@/pages/Home'

export default {
    path: '/',
    component: Landing,
    children: [
        {
            path: 'home',
            component: Home
        },
        {
            path: '*',
            redirect: 'home'
        }
    ]
}


