import Model from '@/pages/Model'
import Portfolio from '@/pages/Portfolio'
import FullRebalance from '@/pages/FullRebalance'
import Dashboard from '@/layouts/Dashboard'
export default {
    path: '/app',
    component: Dashboard,
    meta: { requiresAuth: true },
    children: [
        {
            path: '/model',
            component: Model
          },
          {
            path: '/portfolio',
            component: Portfolio
          },
          {
            path: '/full-rebalance',
            component: FullRebalance
          },
          {
            path: '*',
            redirect: '/model'
          }
    ]
}


