<template>
    <v-dialog max-width="500">
        <v-btn slot="activator" color="primary" class="headline font-weight-thin">login</v-btn>
        <v-card>
            <v-card-text>
                <v-form ref="form" v-model="formHasErrors">
                    <v-text-field
                        autocomplete="off"
                        :rules="[rules.required, rules.email]"
                        v-model="email"
                        label="Email"
                        required>
                    </v-text-field>
                    <v-text-field
                        autocomplete="off"
                        :rules="[rules.required]"
                        v-model="password"
                        label="Password"
                        type="password"
                        required>
                    </v-text-field>
                </v-form>
            </v-card-text>
            <v-card-actions>
                <v-btn flat color="primary">register</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat @click="clear">clear</v-btn>
                <v-btn :disabled="!formHasErrors || loading" :loading="loading" color="primary" @click="submit">submit</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
import utilsMixin from '@/mixins/utilsMixin'

export default {
    mixins: [utilsMixin],
    data: () => ({
        formHasErrors: false,
        email: '',
        password: '',
        loading: false
    }),
    methods: {
        clear() {
            this.$refs.form.reset()
        },
        submit() {
            if(this.$refs.form.validate()) {
                this.loading = true
                window.setTimeout(() => {
                    this.formHasErrors = false
                    this.clear()
                    this.loading = false
                    this.$router.push('/model')
                }, 1500);
            }
        }
    }
}
</script>

<style>

</style>



